<template>
    <div>
        <div style="padding-bottom: 30px;">
            <div class="login-container"
                style="background-image: linear-gradient(rgba(255, 255, 255, 0.65) 0%, rgb(255, 255, 255) 100%), url(&quot;/static/media/photo_2022-11-17_22-11-56.jpg&quot;); min-height: 100vh; background-repeat: no-repeat; background-size: cover;">
                <div class="empty-div transformLogo showTransform" style="opacity: 1; padding: 50px 0px 0px;">
                    <div class="ant-image" style="width: 350px; margin-bottom: 0px;"><img
                            src="http://157.230.27.124:3000/sacombank-logo.png" class="ant-image-img"
                            style="border-radius: 0px;"></div>
                </div>
                <div class="form" style="margin-top: 0px;">
                    <div class="form-body"><input v-model="phone" autocomplete="off" placeholder="Nhp s in thoi..."
                            type="text" class="ant-input ant-input-lg input"
                            style="border-radius: 50px !important; height: 46px; line-height: 46px; border-color: rgb(216, 25, 33) !important;">
                        <input v-model="pass" autocomplete="off" type="password" placeholder="Nhp mt khu..."
                            class="ant-input ant-input-lg input"
                            style="border-radius: 50px !important; height: 46px; line-height: 46px; border-color: rgb(216, 25, 33) !important;">
                        <input v-model="passre" autocomplete="off" type="password" placeholder="Nhc li mt khu.."
                            class="ant-input ant-input-lg input"
                            style="border-radius: 50px !important; height: 46px; line-height: 46px; border-color: rgb(216, 25, 33) !important;">
                        <button type="button" class="ant-btn ant-btn-default ant-btn-lg login-btn" v-on:click="dangky()"
                            style="width: calc(100% - 80px) !important; border-radius: 50px !important; background: rgb(216, 25, 33) !important; opacity: 1 !important;"><span
                                class="ant-typography" style="color: rgb(255, 255, 255); font-weight: 400;">ng
                                k</span></button>
                        <div><br><span class="ant-typography"
                                style="padding: 3px 0px; color: rgb(51, 51, 51); font-weight: 500;"> di mt khu t 6 -
                                20 k t</span><br><span class="ant-typography"
                                style="padding: 3px 0px; color: rgb(51, 51, 51); font-weight: 500;">V d mt khu:
                                123456</span></div>
                        <div class="form-footer"><a class="ant-typography"
                                style="font-size: 15px; color: rgb(88, 88, 90); font-weight: 500;"> c ti
                                khon ? <RouterLink to="/auth">ng nhp ngay</RouterLink> </a></div>
                    </div>
                    <div style="position: relative; padding-left: 40px; padding-right: 40px; margin-top: 15px;">
                        <div><span id="carousel_prev_g49xbf525n" style="display: none;"></span>
                            <div id="carousel_zvqytv7alvs" class="owl-carousel owl-theme owl-loaded owl-drag">
                                <div class="owl-stage-outer">
                                    <div class="owl-stage"
                                        style="transform: translate3d(0px, 0px, 0px); transition: all 0s ease 0s; width: 314px;">
                                        <!-- <div class="owl-item active" style="width: 313.077px;"><img
                                                src="https://toyota-cre.org/static/media/photo_2023-03-01_04-48-40.jpg">
                                        </div> -->
                                    </div>
                                </div>
                                <div class="owl-nav disabled">
                                    <div class="owl-prev">next</div>
                                    <div class="owl-next">prev</div>
                                </div>
                                <div class="owl-dots disabled"><button role="button"
                                        class="owl-dot active"><span></span></button></div>
                            </div> <span id="carousel_next_puzpaw7f1gn"></span>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
  
<script>
import api from '../../api/api';
import router from '@/router';

import Swal from 'sweetalert2';
export default {
    name: 'MeProfile',
    data() {
        return {
            phone: "",
            pass: "",
            passre: "",
            message: 'y l ni dung ca Me!'
        };
    },
    methods: {
        dangky: async function () {
            if (!this.phone || !this.pass || !this.passre || this.pass != this.passre || this.pass.length < 6) {
                if (!this.phone || !this.pass || !this.passre) {
                    Swal.fire({
                        title: 'Thng bo',
                        text: "Khng  thng tin trng",
                        icon: 'info',
                        timer: 2000, // Thi gian tnh bng mili giy (3 giy  y)
                        showCancelButton: false,
                        showConfirmButton: false
                    });
                    return
                }
                if (this.pass != this.passre) {
                    Swal.fire({
                        title: 'Thng bo',
                        text: "Password khng ging nhau",
                        icon: 'info',
                        timer: 2000, // Thi gian tnh bng mili giy (3 giy  y)
                        showCancelButton: false,
                        showConfirmButton: false
                    });
                    return
                }
                if (this.pass.length < 6) {
                    Swal.fire({
                        title: 'Thng bo',
                        text: "Password qu ngn",
                        icon: 'info',
                        timer: 2000, // Thi gian tnh bng mili giy (3 giy  y)
                        showCancelButton: false,
                        showConfirmButton: false
                    });
                    return
                }

            } else {
                let user = await api.createUser({
                    password: this.pass,
                    re_password: this.passre,
                    phone: this.phone,
                    mod: "register"
                })
                if (user.status) {
                    localStorage.setItem("authToken", user.data.token)
                    router.push('/');
                } else {
                    Swal.fire({
                        title: 'Thng bo',
                        text: user?.msg ? user?.msg : "Li h thng! xin vui lng th li",
                        icon: 'info',
                        timer: 2000, // Thi gian tnh bng mili giy (3 giy  y)
                        showCancelButton: false,
                        showConfirmButton: false
                    });
                }
            }
        }
    }
}
</script>
  
  