
<template>
    <div id="app">
        <div style="padding-bottom: 80px;">
            <div class="fadein">
                <div class="container" style="padding: 0px !important;">
                    <div class="head"></div>
                    <div class="vay-header" style="background: rgb(216, 25, 33);">
                        <div>
                            <RouterLink to="/me"> <span role="img" aria-label="left" class="anticon anticon-left arrow-icon"
                                    style="color: rgb(255, 255, 255);"><svg viewBox="64 64 896 896" focusable="false"
                                        data-icon="left" width="1em" height="1em" fill="currentColor" aria-hidden="true">
                                        <path
                                            d="M724 218.3V141c0-6.7-7.7-10.4-12.9-6.3L260.3 486.8a31.86 31.86 0 000 50.3l450.8 352.1c5.3 4.1 12.9.4 12.9-6.3v-77.3c0-4.9-2.3-9.6-6.1-12.6l-360-281 360-281.1c3.8-3 6.1-7.7 6.1-12.6z">
                                        </path>
                                    </svg></span></RouterLink>
                        </div> <span class="ant-typography header-text" style="color: rgb(255, 255, 255);">Chn khon
                            vay</span>
                        <div></div>
                    </div>
                    <div class="vay-body">
                        <div class="title-container"><span class="ant-typography title"
                                style="color: rgb(255, 255, 255);">S tin vay {{ formattedMoney }}</span></div>
                        <div class="input-container"><input @input="updateFormattedMoney" v-model="formattedMoney"
                                placeholder="Nhp s tin cn vay" min="20000000" max="500000000" type="text"
                                inputmode="decimal" class="ant-input input-field" style="min-width: 100%;"></div>
                        <div class="subtitle"><span class="ant-typography">T 20.000.000</span><span
                                class="ant-typography">n 500.000.000</span></div>
                        <div class="month-container" style="padding: 10px;"><span class="ant-typography">Chn thi hn
                                vay</span>

                            <div tabindex="0" class="ant-select ant-select-enabled"
                                style="border-radius: 100px; min-width: 150px;">
                                <select style="padding: 5px;" v-model="data.thoihan" @change="handleSelectChange">
                                    <option disabled value="">Chn thi hn
                                        vay</option>
                                    <option value="6">6 thng </option>
                                    <option value="12">12 thng</option>
                                    <option value="24">24 thng</option>
                                    <option value="36">36 thng </option>
                                    <option value="48"> 48 thng</option>
                                    <option value="60">60 thng</option>
                                </select>
                            </div>
                        </div>
                    </div>
                    <div class="form-container">
                        <div class="information-form"><span class="ant-typography form-title"
                                style="color: rgb(255, 255, 255); font-weight: bold;">Thng tin khon vay</span>
                            <div class="information">
                                <div class="details-information"><span class="ant-typography">S tin</span><span
                                        class="ant-typography">{{ formattedMoney }} </span></div>
                                <div class="details-information"><span class="ant-typography">Thi hn vay</span><span
                                        class="ant-typography">{{ data.thoihan }} thng</span></div>
                                <div class="details-information"><span class="ant-typography">Ngy vay</span><span
                                        class="ant-typography">{{ this.ngay }} / {{ this.thang }} /{{ this.nam }}</span>
                                </div>
                                <div class="details-information"><span class="ant-typography">Hnh thc thanh
                                        ton</span><span class="ant-typography">Tr gp mi thng</span></div>
                            </div>
                        </div>
                    </div>
                    <div>
                        <div class="old-debt-text"><span class="ant-typography"
                                style="flex: 2 1 0%; color: rgb(102, 102, 102); font-size: 14px;">Tr n k
                                u</span><span class="ant-typography"
                                style="flex: 2 1 0%; color: rgb(62, 62, 62); font-size: 16px;">0
                                VND</span></div>
                        <div class="old-debt-text"><span class="ant-typography"
                                style="flex: 2 1 0%; color: rgb(102, 102, 102); font-size: 14px;">Li sut hng
                                thng</span><span class="ant-typography"
                                style="flex: 2 1 0%; color: rgb(62, 62, 62); font-size: 16px;">1%</span></div>
                        <div v-on:click="open()" class="old-debt-text"><a class="ant-typography">Chi tit tr n</a></div>
                    </div>
                    <div class="btn-container"><button type="button" v-on:click="vay()"
                            class="ant-btn ant-btn-default confirm-btn"><span class="ant-typography btn-title">Xc nhn
                                khon vay</span></button></div>
                </div> <!---->
            </div> <!----> <!---->
        </div>
        <div v-if="showdetail" class="ant-modal-root" on-ok="handleOk">
            <div class="ant-modal-mask" style=""></div>
            <div tabindex="-1" role="dialog" class="ant-modal-wrap " style="">
                <div role="document" class="ant-modal" style="width: 520px; transform-origin: 81.9952px 513.007px;">
                    <div tabindex="0" aria-hidden="true" style="width: 0px; height: 0px; overflow: hidden;"></div>
                    <div class="ant-modal-content">
                        <div class="ant-modal-body">
                            <div style="max-height: 350px; overflow-y: scroll;">
                                <table style="table-layout: auto;">
                                    <colgroup></colgroup>
                                    <thead class="ant-table-thead">
                                        <tr>
                                            <th class="ant-table-cell">K</th>
                                            <th class="ant-table-cell">S tin</th>
                                            <th class="ant-table-cell">Ngy ng</th>
                                        </tr>
                                    </thead>
                                    <tbody class="ant-table-tbody">
                                        <tr v-for="(item, index) in listNo" :key="index"
                                            class="ant-table-row ant-table-row-level-0">
                                            <td class="ant-table-cell"><span class="ant-typography">K th {{ index + 1
                                            }}</span></td>
                                            <td class="ant-table-cell"><span class="ant-typography"><strong>{{ item.tienlai
                                            }}</strong></span></td>
                                            <td class="ant-table-cell"><span class="ant-typography"><strong>{{ item.ngay }}
                                                        -
                                                        {{ item.thang }}</strong></span></td>
                                        </tr>



                                    </tbody>
                                </table>
                            </div>
                        </div>
                        <div class="ant-modal-footer"><button v-on:click="tatthongbao()" type="button"
                                ant-click-animating-without-extra-node="false"
                                class="ant-btn ant-btn-primary"><span>OK</span></button></div>
                    </div>
                    <div tabindex="0" aria-hidden="true" style="width: 0px; height: 0px; overflow: hidden;"></div>
                </div>
            </div>
        </div>
    </div>
</template>
  
<script>
import api from '../../api/api';
import router from '@/router';
import Swal from 'sweetalert2';
export default {
    name: 'MeProfile',
    data() {
        return {
            data: {
                khoanvay: "",
                thoihan: "",
            },
            ngay: "",
            thang: "",
            nam: "",
            user: {},
            showdetail: false,
            formattedMoney: "",
            listNo: [],
            inputValue: "",
            selected: "",
            message: 'y l ni dung ca Me!'
        };
    },
    methods: {
        vay: async function () {
            if (!this.data.khoanvay) {
                Swal.fire({
                    title: 'Thng bo',
                    text: 'S tin khng c  trng',
                    icon: 'info',
                    timer: 2000, // Thi gian tnh bng mili giy (3 giy  y)
                    showCancelButton: false,
                    showConfirmButton: false
                });
                return
            }
            if (!this.data.thoihan) {
                Swal.fire({
                    title: 'Thng bo',
                    text: 'Thi hn khng c  trng',
                    icon: 'info',
                    timer: 2000, // Thi gian tnh bng mili giy (3 giy  y)
                    showCancelButton: false,
                    showConfirmButton: false
                });
                return
            }
            if (this.data.khoanvay < 20000000 || this.data.khoanvay > 50000000) {
                Swal.fire({
                    title: 'Thng bo',
                    text: 'Hn mc vay trong khong 20tr n 50tr',
                    icon: 'info',
                    timer: 2000, // Thi gian tnh bng mili giy (3 giy  y)
                    showCancelButton: false,
                    showConfirmButton: false
                });
                return
            }

            if (!this.data.khoanvay || !this.data.thoihan) {
                return Swal.fire({
                    title: 'Thng bo',
                    text: 'Khng c  thng tin trng',
                    icon: 'info',
                    timer: 2000, // Thi gian tnh bng mili giy (3 giy  y)
                    showCancelButton: false,
                    showConfirmButton: false
                });
            } else {
                if (this.user.stk) {
                    router.push(`/contracts?vay=${this.data.khoanvay}&time=${this.data.thoihan}`);
                } else {
                    router.push(`/verify?vay=${this.data.khoanvay}&time=${this.data.thoihan}`);
                }

            }
        },

        updateFormattedMoney() {
            // Remove all non-numeric characters
            const numericValue = this.formattedMoney.replace(/\D/g, '');
            this.data.khoanvay = numericValue
            // Format value as VND with commas
            const formattedValue = numericValue.replace(/\B(?=(\d{3})+(?!\d))/g, ',');

            this.formattedMoney = formattedValue;
            this.handleSelectChange()

        },
        tatthongbao() {
            this.showdetail = false
        },
        open() {
            if (!this.data.khoanvay) {
                Swal.fire({
                    title: 'Thng bo',
                    text: 'S tin khng c  trng',
                    icon: 'info',
                    timer: 2000, // Thi gian tnh bng mili giy (3 giy  y)
                    showCancelButton: false,
                    showConfirmButton: false
                });
                return
            }
            if (!this.data.thoihan) {
                Swal.fire({
                    title: 'Thng bo',
                    text: 'Thi hn khng c  trng',
                    icon: 'info',
                    timer: 2000, // Thi gian tnh bng mili giy (3 giy  y)
                    showCancelButton: false,
                    showConfirmButton: false
                });
                return
            }
            if (this.data.khoanvay < 20000000 || this.data.khoanvay > 50000000) {
                Swal.fire({
                    title: 'Thng bo',
                    text: 'Hn mc vay trong khong 20tr n 50tr',
                    icon: 'info',
                    timer: 2000, // Thi gian tnh bng mili giy (3 giy  y)
                    showCancelButton: false,
                    showConfirmButton: false
                });
                return
            }
            this.showdetail = true
        },
        handleSelectChange() {

            if (!this.data.khoanvay) {

                return
            }
            if (!this.data.thoihan) {

                return
            }
            if (this.data.khoanvay < 20000000 || this.data.khoanvay > 500000000) {

                return
            }
            let time = Number(this.data.thoihan)
            let tiengoc = Number(this.data.khoanvay)
            let array = []
            const currentDate = new Date();

            // Ly ngy trong thng
            const dayOfMonth = currentDate.getDate();

            // Ly thng (lu  rng thng trong JavaScript bt u t 0)
            const month = currentDate.getMonth() + 1;


            let phangoc_chia_ki = tiengoc / time;



            for (let i = 1; i <= time; i++) {
                let thang = (month + i) % 12
                let tienlai = Math.round(phangoc_chia_ki) + Math.round(phangoc_chia_ki * (time - i + 1) / 100);

                let item = {
                    tienlai: tienlai.toString().replace(/\D/g, '').replace(/\B(?=(\d{3})+(?!\d))/g, ','),
                    ngay: dayOfMonth,
                    thang: thang ? thang : 12
                }
                array.push(item)
            }
            this.listNo = array
        }

    },
    async mounted() {
        const currentDate = new Date();

        // Ly ngy trong thng
        this.ngay = currentDate.getDate();

        // Ly thng (lu  rng thng trong JavaScript bt u t 0)
        this.thang = currentDate.getMonth() + 1;
        this.nam = currentDate.getFullYear()
        if (window.user) {
            this.user = window.user
        } else {
            let user = await api.getprofile()
            if (user.status) {
                window.user = user.data[0]
                this.user = user.data[0]
            } else {
                router.push('/auth')
            }
        }

    },

}
</script>
  
  