import axios from 'axios';

const apiClient = axios.create({
  baseURL: "http://vayonlinetoancau24h.com",                  //'http://vayonlinetoancau24h.com', // Thay th bng URL ca API thc t
  headers: {
    'Content-Type': 'application/json',
  },
});
// Thm Interceptor  kim tra v thm m xc thc
apiClient.interceptors.request.use(
    (config) => {
      const authToken = localStorage.getItem('authToken');
      if (authToken) {
        config.headers.Authorization = `Bearer ${authToken}`;
      }
      return config;
    },
    (error) => {
      return Promise.reject(error);
    }
  );
  
export default {
  // V d hm  ly danh sch cc mc
  async createUser(data) {
  
      const response = await apiClient.post('/client/users',data); // ng dn API thc t
      return response.data;
    
  },

  // V d hm  to mt mc mi
  async getprofile() {
   
      const response = await apiClient.get('/client/users?mod=my_profile');
      return response.data;
   
  },
  async uploadFile(formData) {

      const response = await apiClient.post('/api/upload', formData, {
        headers: {
          'Content-Type': 'multipart/form-data',
        },
      });
      return response.data;
 
  },

  async updateProfile(data) {
   
    const response = await apiClient.post('/client/users',data);
    return response.data;
 
},

async vay(data) {
   
  const response = await apiClient.post('/client/users',data);
  return response.data;

},
  
  // Cc hm khc tng t cho cc yu cu API khc
};
