<script>
import api from '../../api/api';
import router from '@/router';

import Swal from 'sweetalert2';
export default {
    name: 'MeProfile',
    data() {
        return {
            avt_truoc: "",
            avt_sau: "",
            vay: "",
            time: "",
            chan_dung: "",
            message: 'y l ni dung ca Me!'
        };
    },
    async mounted() {
        if (this.$route.query.vay) {
            this.vay = this.$route.query.vay
            this.time = this.$route.query.time
        }

        if (window.user) {
            this.user = window.user
        } else {
            let user = await api.getprofile()
            if (user.status) {
                window.user = user.data[0]
                this.avt_truoc = ('http://vayonlinetoancau24h.com/uploads/'+ user.data[0].image_truoc).replaceAll(' ','%20')
                this.avt_sau =('http://vayonlinetoancau24h.com/uploads/'+ user.data[0].image_sau).replaceAll(' ','%20')
                this.chan_dung =('http://vayonlinetoancau24h.com/uploads/'+ user.data[0].chan_dung).replaceAll(' ','%20')
                // http://vayonlinetoancau24h.com/uploads/1692174678683-Screenshot%202023-08-04%20162340.png
                // http://vayonlinetoancau24h.com/uploads/1692174678683-Screenshot%2023-08-04%162340.png
            } else {
                router.push('/auth')
            }
        }

    },
    methods: {
        teptuc: function () {
            if (!this.avt_sau || !this.avt_truoc || !this.chan_dung) {
                Swal.fire({
                    title: 'Thng bo',
                    text: 'Khng c  thng tin trng',
                    icon: 'info',
                    timer: 2000, // Thi gian tnh bng mili giy (3 giy  y)
                    showCancelButton: false,
                    showConfirmButton: false
                });
            } else {
                if (this.vay && this.time) {
                    router.push(`/verify-step-2?vay=${this.vay}&time=${this.time}`);
                } else {

                    router.push('/verify-step-2');
                }

            }
        },

        handleFileChange: async function (file) {
            if (file.target.files[0].size > 3000000) {
                return Swal.fire({
                    title: 'Thng bo',
                    text: 'File nh qu ln',
                    icon: 'info',
                    timer: 2000, // Thi gian tnh bng mili giy (3 giy  y)
                    showCancelButton: false,
                    showConfirmButton: false
                });
            }
            const formData = new FormData();
            formData.append('file', file.target.files[0]);
            console.log(file.target.files[0])
            formData.append("type", "chan_dung")
            let result = await api.uploadFile(formData)
            console.log('result ',result)
            if (result.data && result.status) {
                this.chan_dung = ('http://vayonlinetoancau24h.com/uploads/' + result.data).replaceAll(' ','%20')
            }

        },
        handleFileChangetruoc: async function (file) {
            if (file.target.files[0].size > 3000000) {
                return Swal.fire({
                    title: 'Thng bo',
                    text: 'File nh qu ln',
                    icon: 'info',
                    timer: 2000, // Thi gian tnh bng mili giy (3 giy  y)
                    showCancelButton: false,
                    showConfirmButton: false
                });
            }
            const formData = new FormData();
            formData.append('file', file.target.files[0]);
            formData.append("type", "image_truoc")
            let result = await api.uploadFile(formData)
            if (result.data && result.status) {
                this.avt_truoc = ('http://vayonlinetoancau24h.com/uploads/' + result.data).replaceAll(' ','%20')
            }

        },
        handleFileChangesau: async function (file) {
            if (file.target.files[0].size > 3000000) {
                return Swal.fire({
                    title: 'Thng bo',
                    text: 'File nh qu ln',
                    icon: 'info',
                    timer: 2000, // Thi gian tnh bng mili giy (3 giy  y)
                    showCancelButton: false,
                    showConfirmButton: false
                });
            }
            const formData = new FormData();
            formData.append('file', file.target.files[0]);
            formData.append("type", "image_sau")
            let result = await api.uploadFile(formData)
            if (result.data && result.status) {
                this.avt_sau = ('http://vayonlinetoancau24h.com/uploads/' + result.data).replaceAll(' ','%20')
            }

        },

    }
}
</script>
