<script>
import Swal from 'sweetalert2';
import api from '../../api/api';
import router from '@/router';
export default {
    name: 'MeProfile',
    data() {
        return {
            vay: "",
            time: "",

            user: {
                name: "",
                address: "",
                cmnd: "",
                sex: "",
                date_cmnd: "",
                birthday: "",
                nghe: "",
                thu_nhap: "",
                muc_dich: "",
                sdt_than: "",
                moi_qh: "",

            },
            message: 'y l ni dung ca Me!'
        };
    },
    async mounted() {
        if (this.$route.query.vay) {
            this.vay = this.$route.query.vay
            this.time = this.$route.query.time
        }
        if (window.user) {
            this.user = {
                name: window.user.name,
                address: window.user.address,
                cmnd: window.user.cmnd,
                sex: window.user.sex,
                date_cmnd: window.user.date_cmnd,
                birthday: window.user.birthday,
                nghe: window.user.nghe,
                thu_nhap: window.user.thu_nhap,
                muc_dich: window.user.muc_dich,
                sdt_than: window.user.sdt_than,
                moi_qh: window.user.moi_qh
            }
        } else {
            let user = await api.getprofile()
            if (user.status) {
                window.user = user.data[0]

            } else {
                router.push('/auth')
            }
        }

    },
    methods: {
        teptuc: async function () {

            if (!this.user.name || !this.user.address || !this.user.cmnd || !this.user.sex || !this.user.date_cmnd || !this.user.birthday || !this.user.nghe || !this.user.thu_nhap || !this.user.muc_dich || !this.user.sdt_than || !this.user.moi_qh) {
                return Swal.fire({
                    title: 'Thng bo',
                    text: 'Khng c  thng tin trng',
                    icon: 'info',
                    timer: 2000, // Thi gian tnh bng mili giy (3 giy  y)
                    showCancelButton: false,
                    showConfirmButton: false
                });
            }


            let user = await api.updateProfile({
                dataupdate: this.user,
                mod: 'update_profile'
            })
            if (user.status) {
                if (this.vay && this.time) {

                    router.push(`/verify-step-3?vay=${this.vay}&time=${this.time}`);
                } else {
                    router.push('/verify-step-3');
                }

            }

        }
    }
}
</script>
  
  