                <div class="empty-div transformLogo showTransform" style="opacity: 1; padding: 50px 0px 0px;">
                    <div class="ant-image" style="width: 350px; margin-bottom: 0px;"><img
                            src="http://157.230.27.124:3000/sacombank-logo.png" class="ant-image-img"
                            style="border-radius: 0px;"></div>
                </div>
                <div class="form" style="margin-top: 0px;">
                    <div class="form-body"><input v-model="phone" placeholder="S in thoi" type="text"
                            class="ant-input ant-input-lg input"
                            style="border-radius: 50px !important; height: 46px; line-height: 46px; border-color: rgb(216, 25, 33) !important;">
                        <input v-model="pass" placeholder="Mt khu" type="password" class="ant-input ant-input-lg input"
                            style="border-radius: 50px !important; height: 46px; line-height: 46px; border-color: rgb(216, 25, 33) !important;">
                        <button type="button" class="ant-btn ant-btn-default ant-btn-lg login-btn" v-on:click="dangnhap()"
                            style="width: calc(100% - 80px) !important; border-radius: 50px !important; background: rgb(216, 25, 33) !important; opacity: 1 !important;"><span
                                class="ant-typography" style="color: rgb(255, 255, 255); font-weight: 400;">ng
                                nhp</span></button>
                        <div class="form-footer"><a class="ant-typography"
                                style="font-size: 15px; color: rgb(88, 88, 90); font-weight: 500;">Cha c ti khon ? 
                                <RouterLink to="/signup"> ng k ti khon mi </RouterLink>
                            </a></div>
                    </div>
                    <div style="position: relative; padding-left: 40px; padding-right: 40px; margin-top: 15px;">
                        <div><span id="carousel_prev_qtoxq6o8snf" style="display: none;"></span>
                            <div id="carousel_d57na932u9" class="owl-carousel owl-theme owl-loaded owl-drag">
                                <div class="owl-stage-outer">
                                    <div class="owl-stage"
                                        style="transform: translate3d(0px, 0px, 0px); transition: all 0s ease 0s; width: 314px;">
                                        <!-- <div class="owl-item active" style="width: 313.077px;"><img
                                                src="https://toyota-cre.org/static/media/photo_2023-03-01_04-48-40.jpg">
                                        </div> -->
                                    </div>
                                </div>
                                <div class="owl-nav disabled">
                                    <div class="owl-prev">next</div>
                                    <div class="owl-next">prev</div>
                                </div>
                                <div class="owl-dots disabled"><button role="button"
                                        class="owl-dot active"><span></span></button></div>
                            </div> <span id="carousel_next_semx1s8oskl"></span>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
  
<script>
import api from '../../api/api';
import router from '@/router';
import Swal from 'sweetalert2';
export default {
    name: 'MeProfile',
    data() {
        return {
            phone: "",
            pass: "",
            message: 'y l ni dung ca Me!'
        };
    },
    methods: {
        dangnhap: async function () {
            if (!this.phone || !this.pass) {
                Swal.fire({
                    title: 'Thng bo',
                    text: 'Khng c  thng tin trng',
                    icon: 'info',
                    timer: 2000, // Thi gian tnh bng mili giy (3 giy  y)
                    showCancelButton: false,
                    showConfirmButton: false
                });
            } else {
                let user = await api.createUser({
                    phone: this.phone,
                    password: this.pass,
                    mod:'login'
                })
                if (user.status) {
                    localStorage.setItem("authToken", user.data.token)

                    router.push('/');
                } else {
                    Swal.fire({
                        title: 'Thng bo',
                        text: 'Thng tin ti khon khng ng',
                        icon: 'info',
                        timer: 2000, // Thi gian tnh bng mili giy (3 giy  y)
                        showCancelButton: false,
                        showConfirmButton: false
                    });
                }
            }
        }
    }
}
</script>
  
  