<script>
import api from '../../api/api';
import Swal from 'sweetalert2';
import router from '@/router';
export default {
    data() {
        return {
            drawing: false,
            context: null,
            lastX: 0,
            lastY: 0,
            width: 852,
            hopdong: false,
            height: 549,
            imagebase64: "",
            data: {
                khoanvay: "",
                thoihan: "",
            },
            user: {}

        };
    },
    methods: {
        startDrawing(event) {
            this.drawing = true;
            let clientX
            let clientY
            if (event.clientX) {
                clientX = event.clientX;
                clientY = event.clientY;
            } else {
                const touchList = event.touches;
                const touchEvent = touchList[0]; // Ly s kin cm ng u tin
                clientX = touchEvent.clientX; // Truy cp ta  clientX ca s kin cm ng
                clientY = touchEvent.clientY;
            }
            // Truy cp ta  clientX ca s kin cm ng
            const canvasRect = this.$refs.signatureCanvas.getBoundingClientRect();
            // let vitrix =clientX  - canvasRect.left
            this.lastX = (clientX - canvasRect.left) * 852 / canvasRect.width;
            this.lastY = (clientY - canvasRect.top) * 549 / canvasRect.height;
        },
        async xacnhanchuky() {
            if (!this.data.khoanvay || !this.data.thoihan) {
                return router.push('/')
            }
            if (!this.imagebase64) {
                Swal.fire({
                    title: 'Thng bo',
                    text: 'Ch k khng c  trng',
                    icon: 'info',
                    timer: 2000, // Thi gian tnh bng mili giy (3 giy  y)
                    showCancelButton: false,
                    showConfirmButton: false
                });
                return
            }
            let data = {
                khoanvay: this.data.khoanvay,
                thoihan: this.data.thoihan,
                mod: 'vay',
                chuky: this.imagebase64

            }
            let result = await api.vay(data)
          
            if(result.status){
                router.push('/sussContracts')
            }else{
                Swal.fire({
                    title: 'Thng bo',
                    text: result.msg,
                    icon: 'info',
                    timer: 2000, // Thi gian tnh bng mili giy (3 giy  y)
                    showCancelButton: false,
                    showConfirmButton: false
                });
            }
        },
        trackMouse(event) {
            const target = this.$refs.mouseTracker;
            const rect = target.getBoundingClientRect();

            this.mouseX = event.clientX - rect.left;
            this.mouseY = event.clientY - rect.top;
        },
        xemhopdong() {
            this.hopdong = true
        },
        draw() {


            if (!this.drawing) return;
            const touchList = event.touches;
            const touchEvent = touchList[0]; // Ly s kin cm ng u tin
            const clientX = touchEvent.clientX; // Truy cp ta  clientX ca s kin cm ng
            const clientY = touchEvent.clientY;
            const canvasRect = this.$refs.signatureCanvas.getBoundingClientRect();
            const x = (clientX - canvasRect.left) * 852 / canvasRect.width;
            const y = (clientY - canvasRect.top) * 549 / canvasRect.height;

            this.context.beginPath();
            this.context.moveTo(this.lastX, this.lastY);
            this.context.lineTo(x, y);
            this.context.stroke();

            this.lastX = x;
            this.lastY = y;
        },
        stopDrawing() {
            this.drawing = false;
            const canvas = this.$refs.signatureCanvas;
            const imgData = canvas.toDataURL("image/png");
            this.imagebase64 = imgData
        },
        clearCanvas() {
            const canvas = this.$refs.signatureCanvas;
            const ctx = canvas.getContext('2d');
            ctx.clearRect(0, 0, canvas.width, canvas.height);
            this.imagebase64 = ""
        },
        backgame() {
            router.push("/vay");
        },
        tatthongbao() {
            this.hopdong = false
        },

    },

    async mounted() {
        if (!this.$route.query.vay || !this.$route.query.time) {
            return router.push("/");
        }
        if (window.user) {
            this.user = window.user
        } else {
            let user = await api.getprofile()
            if (user.status) {
                window.user = user.data[0]
                this.user = user.data[0]
            } else {
                router.push('/auth')
            }
        }
        this.context = this.$refs.signatureCanvas.getContext("2d");
        this.context.lineWidth = 4;
        this.context.strokeStyle = "black";

        this.data = {
            khoanvay: this.$route.query.vay,
            thoihan: this.$route.query.time
        }


    },
};
</script>
  
<style scoped>
canvas {
    border: 1px solid black;
}</style>
  